@import url("./utils/reset.css");
@import url("./utils/utils.css");

:root {
    --header-color: rgb(255 255 255 / 5%);;
    --app-background-white: #e5e7ef;
    --grey-home:#f2f2f2;
    --grey-login:#d8d8d8;
    --h-color: #232f2f;
    --hover-color: #89A6D7;
    --logo-color:#130B68;
    --logo-color-2:#43619f;
    --placeholder-color: #d1dcef;
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

header {
    height: 60px;
    background-color: var(--header-color);
    color: #F0B90B;
}

header h1 {
    font-size: 1.8rem;
    font-family: "Oxygen", sans-serif;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    line-height: 60px;
    letter-spacing: 0.2px;
}

nav ul {
    display: flex;
    gap: 15px;
    padding-right: 15px;
    margin-right: 15px;
    margin-top: 0.2px;
    border-right: 0.1px solid #ddd;
}

nav ul li {
    font-size: 1.7rem;
    font-family: 600;
    cursor: pointer;
    color: #fff;
    transition: border-bottom ease-in-out 200ms;
    border-bottom: 0.2px solid transparent;
}

nav ul li:hover {
    border-bottom: 0.2px solid #F0B90B;
}

